<!---->
<template>
  <div id="applicationManage">
    <el-row>
      <el-form :inline="true" :model="searchItem" ref="searchItem">
        <el-form-item label="" prop="name">
          <el-input v-model="searchItem.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="searchItem.type" :placeholder="appTypeArr.title">
            <el-option
              v-for="item in appTypeArr.options"
              :key="item.ItemCode"
              :label="item.ItemName"
              :value="item.ItemCode"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search(1)"></el-button>
          <el-button type="default" @click="reset('searchItem')"></el-button>
        </el-form-item>
      </el-form>
    </el-row>
    <el-row>
      <el-col :span="22">
        <div>
          <el-table
            :data="tableData"
            border
            size="medium"
            class="applicationTable"
            @selection-change="getRowDatas"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="appId" width="120" label="ID">
              <template slot-scope="scope">
                <el-button
                  @click="detailRow(scope.row)"
                  type="text"
                  size="medium"
                >{{scope.row.appId}}</el-button>
              </template>
            </el-table-column>
            <el-table-column prop="appName" width="120" label=""></el-table-column>
            <el-table-column prop="appType" label=""></el-table-column>
            <el-table-column prop="vendor" width="150" label=""></el-table-column>
            <el-table-column prop="version" label=""></el-table-column>
            <el-table-column prop="appReleaseTime" width="120" label=""></el-table-column>
            <el-table-column prop="containerType" width="120" label=""></el-table-column>
            <el-table-column prop="containerVersion" label=""></el-table-column>
            <el-table-column prop="options" label="">
              <template slot-scope="scope">
                <!-- <el-button @click="editRow(scope.row)" type="text" size="medium"></!-->
                <el-button @click="delRow(scope.row)" type="text" size="medium"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-row style="margin:20px 0px">
            <el-button type="primary" round icon="el-icon-plus" @click="add('dialogForm')"></el-button>
            <el-pagination
              background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="1"
              :page-sizes="[5, 10,15]"
              :page-size="tableLimit"
              layout="total, sizes, prev, pager, next"
              :total="tableSize"
              style="float: right"
            ></el-pagination>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <!-- / -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogFormVisible" width="70%">
      <el-form
        :model="dialogForm"
        :rules="dialogRules"
        ref="dialogForm"
        label-width="150px"
        class="acForm"
      >
        <el-row>
          <el-col :span="12">
            <el-form-item label="ID" prop="appId">
              <span v-if="ifDialogDetail">{{dialogForm.appId}}</span>
              <el-input v-if="!ifDialogDetail" v-model="dialogForm.appId" placeholder="ID"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="appName">
              <span v-if="ifDialogDetail">{{dialogForm.appName}}</span>
              <el-input v-if="!ifDialogDetail" v-model="dialogForm.appName" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" prop="appType">
              <span v-if="ifDialogDetail">{{dialogForm.appType}}</span>
              <el-select
                v-if="!ifDialogDetail"
                v-model="dialogForm.appType"
                :placeholder="appTypeArr.title"
                style="width:100%"
              >
                <el-option
                  v-for="item in appTypeArr.options"
                  :key="item.ItemCode"
                  :label="item.ItemName"
                  :value="item.ItemCode"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
           <el-col :span="12">
            <el-form-item label="" prop="appStoreId">
              <span v-if="ifDialogDetail">{{dialogForm.appStoreId}}</span>
              <el-input v-if="!ifDialogDetail" v-model="dialogForm.appStoreId" placeholder=""></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <span v-if="ifDialogDetail">{{dialogForm.version}}</span>
              <el-input v-if="!ifDialogDetail" v-model="dialogForm.version" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <span v-if="ifDialogDetail">{{dialogForm.appReleaseTime}}</span>
              <el-date-picker
                v-model="dialogForm.appReleaseTime"
                 v-if="!ifDialogDetail"
                type="datetime"
                placeholder=""
                style="width:90%"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <span v-if="ifDialogDetail">{{dialogForm.containerType}}</span>
              <el-input
                v-if="!ifDialogDetail"
                v-model="dialogForm.containerType"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <span v-if="ifDialogDetail">{{dialogForm.containerVersion}}</span>
              <el-input
                v-if="!ifDialogDetail"
                v-model="dialogForm.containerVersion"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <span v-if="ifDialogDetail">{{dialogForm.vendor}}</span>
              <el-input v-if="!ifDialogDetail" v-model="dialogForm.vendor" placeholder=""></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <span v-if="ifDialogDetail">{{dialogForm.fileSizeMB}}</span>
              <el-input
                v-if="!ifDialogDetail"
                v-model="dialogForm.fileSizeMB"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <span v-if="ifDialogDetail">{{dialogForm.minDataDiskMB}}</span>
              <el-input
                v-if="!ifDialogDetail"
                v-model="dialogForm.minDataDiskMB"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="CPU">
              <span v-if="ifDialogDetail">{{dialogForm.minVcpus}}</span>
              <el-input
                v-if="!ifDialogDetail"
                v-model="dialogForm.minVcpus"
                placeholder="CPU"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <span v-if="ifDialogDetail">{{dialogForm.physicalInterfaces}}</span>
              <el-input
                v-if="!ifDialogDetail"
                v-model="dialogForm.physicalInterfaces"
                placeholder=""
              ></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="url">
              <span v-if="ifDialogDetail">{{dialogForm.downloadURL}}</span>
              <el-input
                v-if="!ifDialogDetail"
                v-model="dialogForm.downloadURL"
                placeholder="url"
              ></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="url">
              <span v-if="ifDialogDetail">{{dialogForm.logo}}</span>
              <el-input v-if="!ifDialogDetail" v-model="dialogForm.logo" placeholder="url"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="submitForm('dialogForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "applicationManage",
  data() {
    return {
      tableSize: 0,
      tableLimit: 10,
      selectedRow: [],
      searchItem: {
        name: "",
        type: ""
      },
      appTypeArr: {
        title: "",
        options: []
      },
      containerTypeArr: {
        title: "",
        options: [
          {
            value: "1",
            label: "1"
          },
          {
            value: "2",
            label: "2"
          },
          {
            value: "3",
            label: "3"
          }
        ]
      },
      tableData: [],
      dialogTitle: "",
      dialogFormVisible: false,
      ifDialogDetail: false,
      dialogForm: {
        appId:"",
        appName: "",
        appType: "",
        appReleaseTime:"",
        vendor: "",
        version: "",
        containerType: "",
        containerVersion: "",
        downloadURL:"",
        fileSizeMB:"",
        logo:"",
        minVcpus:"",
        physicalInterfaces:""
      },
      dialogRules: {
        appId: [
          { required: true, message: "ID", trigger: "change" }
        ],
        appName: [
          { required: true, message: "", trigger: "blur" }
        ],
        appType: [
          { required: true, message: "", trigger: "change" }
        ],
        appStoreId: [
          { required: true, message: " ", trigger: "change" }
        ]
      }
    };
  },
  mounted() {
    this.getAddDialogType();
    this.search(1);
  },
  methods: {
    //
    getAddDialogType() {
      this.$axios
        .post(baseUrl + "/admin/app/getAllTypes")
        .then(res => {
          if (res.data.success) {
            this.appTypeArr.options = res.data.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    search(page) {
      let param = {};
      Object.keys(this.searchItem).forEach(item => {
        if (this.searchItem[item] !== "") param[item] = this.searchItem[item];
      });
      this.$axios
        .post(
          baseUrl +
            "/admin/app/files?" +
            "pageIndex=" +
            page +
            "&pageSize=" +
            this.tableLimit,
          param
          // {headers: {"Content-Type": "application/json"}}
        )
        .then(res => {
          this.tableSize = res.data.data.total;
          this.tableData = res.data.data.records;
        })
        .catch(err => {
          console.log(err);
        });
    },
    reset(formName) {
      this.$refs[formName].resetFields();
    },
    getRowDatas(row) {
      this.selectedRow = row;
      console.info(row);
    },
    add(formName) {
      this.dialogTitle = "";
      this.ifDialogDetail = false;
      this.dialogForm = {};
      this.dialogFormVisible = true;
      // this.$nextTick(() => {
      //   this.$refs[formName].resetFields();
      // });
    },
    detailRow(row) {
      this.$nextTick(() => {
        this.$refs['dialogForm'].resetFields();
      });
      this.dialogTitle = "";
      this.ifDialogDetail = true;
      this.dialogForm = Object.assign({}, row);
      this.dialogFormVisible = true;
    },
    editRow(row) {
      this.dialogTitle = "";
      this.dialogForm = Object.assign({}, row);
      this.ifDialogDetail = false;
      this.dialogFormVisible = true;
    },
    delRow(row) {
      this.$confirm("     '" + row.appName + "'     ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$axios.post(baseUrl+'/admin/app/delete',{
            appId:row.appId,
          }).then((res)=>{
            this.$message({
            type: "success",
            message: "!"
          });
            this.search(1);
          }).catch((err)=>{
            console.log(err);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    submitForm(formName) {
      debugger;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios.post(baseUrl+'/admin/app/add',
            this.dialogForm
          ).then((res)=>{
          this.dialogFormVisible = false;
          this.$message({
            message: "",
            type: "success"
          });
          this.$refs[formName].resetFields();
            this.search(1);
          }).catch((err)=>{
            console.log(err);
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleSizeChange(val) {
      this.tableLimit = val;
      this.search(1);
    },
    handleCurrentChange(val) {
      this.search(val);
    }
  }
};
</script>

<style scoped>
.applicationTable {
  max-height: 500px;
  overflow: auto;
}
</style>



